.App {
    text-align: center;
    font-family: whomp, sans-serif;
    font-weight: 400;
    font-style: normal;
    background-color: white;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    font-size: calc(45px + 2vmin);
    color: white;
}

p {
  color: rgba(258, 218, 240, 0.3);
  margin-block-start: .125em;
  margin-block-end: .125em;
}


a {
  color: rgb(258, 218, 240);
  font-size: calc(55px + 2vmin);
}
